import interfaces from '../../utils/interfaces.js';
 import popupLayer from '../../components/popup-layer/popup-layer.vue';
 import menus from "../../components/menu.vue";
 import {
         mapState,
         mapMutations
     } from 'vuex';
export default {
	components:{
			popupLayer,
			menus
	},
	computed: {
	            ...mapState(['type'])
	        },
	data() {
		return {
			goodsList:[],
			categoryList:[],
				 pageNum:1,
				 hasNext:true,
				 searchName:"",
				
			
			}
	},

	onLoad() {
		this.getData();
		this.getgoods(-1);
	
	},
	 watch:{
		 
		 searchName(){
			 if(this.searchName==""){
				this.getgoods(-1);
			 }
		 },
		 
		 type(){
			this.getgoods(-1);
		 }
	    },
		onShow(id) {
			if(!!id){
				console.log(id);
				this.getgoods(id);
			}
		},
	methods: {
		search(){
			if(this.searchName==""){
				return;
			}
			
			this.request({
				url: interfaces.searchGoods,
				data:{
					name:this.searchName,
					
					type:this.type
				},
				success: ((res) => {
					console.log(res)
					if(res.errno==0){
					
						this.hasNext=res.data.hasNextPage;
					this.goodsList=	res.data; 
					}
					
				
			
				})
			})
			
		},
		intocategory(){
			uni.navigateTo({
				url:"../categorys/categorys"
			})
		},
		intoproduct(id){
			uni.navigateTo({
				url:"../product/product?id="+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		},
		getData(){
			this.request({
				url: interfaces.getcategory,
				success: ((res) => {
					console.log(res);
					if(res.errno==0){
					this.categoryList=res.data;
					}
					
				
			
				})
			})
		},
		onReachBottom(){
			
				if(this.hasNext==true){
					this.request({
						url: interfaces.getGoodsByCid,
						data: {
						    pageNum:this.pageNum,
						    pageSize:30,
						    cid:-1,
						    type:this.type
						    },
						success: ((res) => {
							console.log(res)
							if(res.errno==0){
								this.pageNum=res.data.pageNum+1;
								this.hasNext=res.data.hasNextPage;
								for(let i=0;i<res.data.list.length;i++){
									this.goodsList.push(res.data.list[i]);
									
								}
								
							}
							
						
					
						})
					})
				}else{
					if(this.type=="c"){
						setTimeout(function(){
						         uni.showToast({
						                        icon:"none",
						         				duration:2000,
						                        title:"没有更多了"
						                    });
						          }, 500)
					}else{
						setTimeout(function(){
						         uni.showToast({
						                        icon:"none",
						         				duration:2000,
						                        title:"No more"
						                    });
						          }, 500)
						
					}
				}
		},
		getgoods(id){
			this.pageNum=1;
			this.request({
				url: interfaces.getGoodsByCid,
				data:{
					pageNum:this.pageNum,
					pageSize:30,
					cid:id,
					type:this.type
				},
				success: ((res) => {
					console.log(res)
					if(res.errno==0){
					
						this.hasNext=res.data.hasNextPage;
					this.goodsList=	res.data.list; 
					}
					
				
			
				})
			})
		},
		changeone(one){
			
			this.UnitSelected=one.id;
			this.onename=one.name;
			this.hasNext=true;
			this.pageNum=1;
			this.goodsList=[];
			this.getgoods(one.id);
			
			this.close();
		},
		onPullDownRefresh() {
		        //监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
				 this.hasNext=true;
				 this.pageNum=1;
				 this.goodsList=[];
				this.hasNext=true;
				
				this.getData();
				this.getgoods(-1);
						
				  
		    }
		
		
	
		
	}

}
